<template>
<div :style='{"width":"80%","padding":"20px 0","margin":"0 auto","position":"relative","background":"#FCFAFF"}'>
    <el-form
	  :style='{"width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="100px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="酒店名称" prop="jiudianmingcheng">
            <el-input v-model="ruleForm.jiudianmingcheng" 
                placeholder="酒店名称" clearable :disabled=" false  ||ro.jiudianmingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="酒店图片" v-if="type!='cross' || (type=='cross' && !ro.jiudiantupian)" prop="jiudiantupian">
            <file-upload
            tip="点击上传酒店图片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.jiudiantupian?ruleForm.jiudiantupian:''"
            @change="jiudiantupianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' class="upload" v-else label="酒店图片" prop="jiudiantupian">
                <img v-if="ruleForm.jiudiantupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jiudiantupian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jiudiantupian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="类别" prop="leibie">
            <el-select v-model="ruleForm.leibie" placeholder="请选择类别" :disabled=" false  ||ro.leibie" >
              <el-option
                  v-for="(item,index) in leibieOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="星级" prop="xingji">
            <el-select v-model="ruleForm.xingji" placeholder="请选择星级" :disabled=" false  ||ro.xingji" >
              <el-option
                  v-for="(item,index) in xingjiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="酒店地址" prop="jiudiandizhi">
            <el-input v-model="ruleForm.jiudiandizhi" 
                placeholder="酒店地址" clearable :disabled=" false  ||ro.jiudiandizhi"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="房间类型" prop="fangjianleixing">
            <el-select v-model="ruleForm.fangjianleixing" placeholder="请选择房间类型" :disabled=" false  ||ro.fangjianleixing" >
              <el-option
                  v-for="(item,index) in fangjianleixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="房间价格" prop="fangjianjiage">
			<el-input-number v-model="ruleForm.fangjianjiage" placeholder="房间价格" :disabled="ro.fangjianjiage"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="预订天数" prop="yudingtianshu">
            <el-input v-model.number="ruleForm.yudingtianshu" 
                placeholder="预订天数" clearable :disabled=" false  ||ro.yudingtianshu"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="应付价格" prop="yingfujiage">
              <el-input v-model="yingfujiage" placeholder="应付价格" disabled></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="预订时间" prop="yudingshijian">
              <el-date-picker
				  :disabled=" false  ||ro.yudingshijian"
                  format="yyyy 年 MM 月 dd 日"
                  value-format="yyyy-MM-dd"
                  v-model="ruleForm.yudingshijian" 
                  type="date"
                  placeholder="预订时间">
              </el-date-picker> 
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="用户账号" prop="yonghuzhanghao">
            <el-input v-model="ruleForm.yonghuzhanghao" 
                placeholder="用户账号" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="用户姓名" prop="yonghuxingming">
            <el-input v-model="ruleForm.yonghuxingming" 
                placeholder="用户姓名" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="预订备注" prop="yudingbeizhu">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="预订备注"
              v-model="ruleForm.yudingbeizhu">
            </el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"5px","background":"#A293B6","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"none","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"5px","background":"#9e9e9e","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				jiudianmingcheng : false,
				jiudiantupian : false,
				leibie : false,
				xingji : false,
				jiudiandizhi : false,
				fangjianleixing : false,
				fangjianjiage : false,
				yudingtianshu : false,
				yingfujiage : false,
				yudingshijian : false,
				yonghuzhanghao : false,
				yudingbeizhu : false,
				yonghuxingming : false,
				sfsh : false,
				shhf : false,
				ispay : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          jiudianmingcheng: '',
          jiudiantupian: '',
          leibie: '',
          xingji: '',
          jiudiandizhi: '',
          fangjianleixing: '',
          fangjianjiage: '',
          yudingtianshu: '',
          yingfujiage: '',
          yudingshijian: '',
          yonghuzhanghao: '',
          yudingbeizhu: '',
          yonghuxingming: '',
          ispay: '',
        },
        leibieOptions: [],
        xingjiOptions: [],
        fangjianleixingOptions: [],


        rules: {
          jiudianmingcheng: [
            { required: true, message: '酒店名称不能为空', trigger: 'blur' },
          ],
          jiudiantupian: [
          ],
          leibie: [
            { required: true, message: '类别不能为空', trigger: 'blur' },
          ],
          xingji: [
            { required: true, message: '星级不能为空', trigger: 'blur' },
          ],
          jiudiandizhi: [
          ],
          fangjianleixing: [
          ],
          fangjianjiage: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          yudingtianshu: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          yingfujiage: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          yudingshijian: [
          ],
          yonghuzhanghao: [
          ],
          yudingbeizhu: [
          ],
          yonghuxingming: [
          ],
          sfsh: [
          ],
          shhf: [
          ],
          ispay: [
          ],
        },
		centerType: false,
      };
    },
    computed: {


        yingfujiage:{
            get: function () {
                return 1*this.ruleForm.fangjianjiage*this.ruleForm.yudingtianshu
            }
        },

    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
      this.ruleForm.yudingshijian = this.getCurDate()
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='jiudianmingcheng'){
              this.ruleForm.jiudianmingcheng = obj[o];
              this.ro.jiudianmingcheng = true;
              continue;
            }
            if(o=='jiudiantupian'){
              this.ruleForm.jiudiantupian = obj[o].split(",")[0];
              this.ro.jiudiantupian = true;
              continue;
            }
            if(o=='leibie'){
              this.ruleForm.leibie = obj[o];
              this.ro.leibie = true;
              continue;
            }
            if(o=='xingji'){
              this.ruleForm.xingji = obj[o];
              this.ro.xingji = true;
              continue;
            }
            if(o=='jiudiandizhi'){
              this.ruleForm.jiudiandizhi = obj[o];
              this.ro.jiudiandizhi = true;
              continue;
            }
            if(o=='fangjianleixing'){
              this.ruleForm.fangjianleixing = obj[o];
              this.ro.fangjianleixing = true;
              continue;
            }
            if(o=='fangjianjiage'){
              this.ruleForm.fangjianjiage = obj[o];
              this.ro.fangjianjiage = true;
              continue;
            }
            if(o=='yudingtianshu'){
              this.ruleForm.yudingtianshu = obj[o];
              this.ro.yudingtianshu = true;
              continue;
            }
            if(o=='yingfujiage'){
              this.ruleForm.yingfujiage = obj[o];
              this.ro.yingfujiage = true;
              continue;
            }
            if(o=='yudingshijian'){
              this.ruleForm.yudingshijian = obj[o];
              this.ro.yudingshijian = true;
              continue;
            }
            if(o=='yonghuzhanghao'){
              this.ruleForm.yonghuzhanghao = obj[o];
              this.ro.yonghuzhanghao = true;
              continue;
            }
            if(o=='yudingbeizhu'){
              this.ruleForm.yudingbeizhu = obj[o];
              this.ro.yudingbeizhu = true;
              continue;
            }
            if(o=='yonghuxingming'){
              this.ruleForm.yonghuxingming = obj[o];
              this.ro.yonghuxingming = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
                this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
				this.ro.yonghuzhanghao = true;
            }
            if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
                this.ruleForm.yonghuxingming = json.yonghuxingming;
				this.ro.yonghuxingming = true;
            }
          }
        });
        this.leibieOptions = "民宿,连锁酒店,快捷酒店,豪华酒店".split(',')
        this.xingjiOptions = "一星,二星,三星,四星,五星".split(',')
        this.fangjianleixingOptions = "单人间,大床房,双人间,三人间,标准套间,豪华套间".split(',')

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`jiudianyuding/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
		this.ruleForm.ispay = '未支付'
			this.ruleForm.yingfujiage = this.yingfujiage
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('jiudianyuding/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`jiudianyuding/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`jiudianyuding/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      jiudiantupianUploadChange(fileUrls) {
          this.ruleForm.jiudiantupian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
