<template>
<div :style='{"width":"80%","padding":"20px 0","margin":"0 auto","position":"relative","background":"#FCFAFF"}'>
    <el-form
	  :style='{"width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="100px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点名称" prop="jingdianmingcheng">
            <el-input v-model="ruleForm.jingdianmingcheng" 
                placeholder="景点名称" clearable :disabled=" false  ||ro.jingdianmingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点照片" v-if="type!='cross' || (type=='cross' && !ro.jingdianzhaopian)" prop="jingdianzhaopian">
            <file-upload
            tip="点击上传景点照片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.jingdianzhaopian?ruleForm.jingdianzhaopian:''"
            @change="jingdianzhaopianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' class="upload" v-else label="景点照片" prop="jingdianzhaopian">
                <img v-if="ruleForm.jingdianzhaopian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jingdianzhaopian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jingdianzhaopian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="旅游地址" prop="shengfen">
            <el-select v-model="ruleForm.shengfen" placeholder="请选择旅游地址" :disabled=" false  ||ro.shengfen" >
              <el-option
                  v-for="(item,index) in shengfenOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="行程规划" prop="xingchengguihua">
            <el-input v-model="ruleForm.xingchengguihua" 
                placeholder="行程规划" clearable :disabled=" false  ||ro.xingchengguihua"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="交通指南" prop="jiaotongzhinan">
            <el-input v-model="ruleForm.jiaotongzhinan" 
                placeholder="交通指南" clearable :disabled=" false  ||ro.jiaotongzhinan"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="住宿推荐" prop="zhusutuijian">
            <el-input v-model="ruleForm.zhusutuijian" 
                placeholder="住宿推荐" clearable :disabled=" false  ||ro.zhusutuijian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="美食推荐" prop="meishituijian">
            <el-input v-model="ruleForm.meishituijian" 
                placeholder="美食推荐" clearable :disabled=" false  ||ro.meishituijian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="用户账号" prop="yonghuzhanghao">
            <el-input v-model="ruleForm.yonghuzhanghao" 
                placeholder="用户账号" clearable :disabled=" false  ||ro.yonghuzhanghao"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="用户姓名" prop="yonghuxingming">
            <el-input v-model="ruleForm.yonghuxingming" 
                placeholder="用户姓名" clearable :disabled=" false  ||ro.yonghuxingming"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点介绍" prop="jingdianjieshao">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="景点介绍"
              v-model="ruleForm.jingdianjieshao">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="娱乐活动" prop="yulehuodong">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="娱乐活动"
              v-model="ruleForm.yulehuodong">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="注意事项" prop="zhuyishixiang">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="注意事项"
              v-model="ruleForm.zhuyishixiang">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="预算建议" prop="yusuanjianyi">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="预算建议"
              v-model="ruleForm.yusuanjianyi">
            </el-input>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"5px","background":"#A293B6","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"none","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"5px","background":"#9e9e9e","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				jingdianmingcheng : false,
				jingdianzhaopian : false,
				shengfen : false,
				xingchengguihua : false,
				jiaotongzhinan : false,
				zhusutuijian : false,
				meishituijian : false,
				jingdianjieshao : false,
				yulehuodong : false,
				zhuyishixiang : false,
				yusuanjianyi : false,
				yonghuzhanghao : false,
				yonghuxingming : false,
				discussnum : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          jingdianmingcheng: '',
          jingdianzhaopian: '',
          shengfen: '',
          xingchengguihua: '',
          jiaotongzhinan: '',
          zhusutuijian: '',
          meishituijian: '',
          jingdianjieshao: '',
          yulehuodong: '',
          zhuyishixiang: '',
          yusuanjianyi: '',
          yonghuzhanghao: '',
          yonghuxingming: '',
          discussnum: '',
        },
        shengfenOptions: [],


        rules: {
          jingdianmingcheng: [
          ],
          jingdianzhaopian: [
          ],
          shengfen: [
          ],
          xingchengguihua: [
          ],
          jiaotongzhinan: [
          ],
          zhusutuijian: [
          ],
          meishituijian: [
          ],
          jingdianjieshao: [
          ],
          yulehuodong: [
          ],
          zhuyishixiang: [
          ],
          yusuanjianyi: [
          ],
          yonghuzhanghao: [
          ],
          yonghuxingming: [
          ],
          discussnum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='jingdianmingcheng'){
              this.ruleForm.jingdianmingcheng = obj[o];
              this.ro.jingdianmingcheng = true;
              continue;
            }
            if(o=='jingdianzhaopian'){
              this.ruleForm.jingdianzhaopian = obj[o].split(",")[0];
              this.ro.jingdianzhaopian = true;
              continue;
            }
            if(o=='shengfen'){
              this.ruleForm.shengfen = obj[o];
              this.ro.shengfen = true;
              continue;
            }
            if(o=='xingchengguihua'){
              this.ruleForm.xingchengguihua = obj[o];
              this.ro.xingchengguihua = true;
              continue;
            }
            if(o=='jiaotongzhinan'){
              this.ruleForm.jiaotongzhinan = obj[o];
              this.ro.jiaotongzhinan = true;
              continue;
            }
            if(o=='zhusutuijian'){
              this.ruleForm.zhusutuijian = obj[o];
              this.ro.zhusutuijian = true;
              continue;
            }
            if(o=='meishituijian'){
              this.ruleForm.meishituijian = obj[o];
              this.ro.meishituijian = true;
              continue;
            }
            if(o=='jingdianjieshao'){
              this.ruleForm.jingdianjieshao = obj[o];
              this.ro.jingdianjieshao = true;
              continue;
            }
            if(o=='yulehuodong'){
              this.ruleForm.yulehuodong = obj[o];
              this.ro.yulehuodong = true;
              continue;
            }
            if(o=='zhuyishixiang'){
              this.ruleForm.zhuyishixiang = obj[o];
              this.ro.zhuyishixiang = true;
              continue;
            }
            if(o=='yusuanjianyi'){
              this.ruleForm.yusuanjianyi = obj[o];
              this.ro.yusuanjianyi = true;
              continue;
            }
            if(o=='yonghuzhanghao'){
              this.ruleForm.yonghuzhanghao = obj[o];
              this.ro.yonghuzhanghao = true;
              continue;
            }
            if(o=='yonghuxingming'){
              this.ruleForm.yonghuxingming = obj[o];
              this.ro.yonghuxingming = true;
              continue;
            }
            if(o=='discussnum'){
              this.ruleForm.discussnum = obj[o];
              this.ro.discussnum = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
            if((json.yonghuzhanghao!=''&&json.yonghuzhanghao) || json.yonghuzhanghao==0){
                this.ruleForm.yonghuzhanghao = json.yonghuzhanghao;
				this.ro.yonghuzhanghao = true;
            }
            if((json.yonghuxingming!=''&&json.yonghuxingming) || json.yonghuxingming==0){
                this.ruleForm.yonghuxingming = json.yonghuxingming;
				this.ro.yonghuxingming = true;
            }
          }
        });
        this.$http.get('option/shengfen/shengfen', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.shengfenOptions = res.data.data;
          }
        });

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`lvyoujihua/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('lvyoujihua/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`lvyoujihua/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`lvyoujihua/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      jingdianzhaopianUploadChange(fileUrls) {
          this.ruleForm.jingdianzhaopian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.el-date-editor.el-input {
		width: auto;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
	  padding: 0 10px 0 0;
	  color: #000;
	  font-weight: 500;
	  width: 100px;
	  font-size: 14px;
	  line-height: 40px;
	  text-align: center;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__content {
	  margin-left: 100px;
	}
	
	.add-update-preview .el-input /deep/ .el-input__inner {
	  border: 1px solid #E2E3E5;
	  border-radius: 30px;
	  padding: 0 12px;
