<template>
<div :style='{"width":"80%","padding":"20px 0","margin":"0 auto","position":"relative","background":"#FCFAFF"}'>
    <el-form
	  :style='{"width":"100%","position":"relative"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      :rules="rules"
      label-width="100px"
    >
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点编号" prop="jingdianbianhao">
              <el-input v-model="ruleForm.jingdianbianhao" placeholder="景点编号" disabled></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点名称" prop="jingdianmingcheng">
            <el-input v-model="ruleForm.jingdianmingcheng" 
                placeholder="景点名称" clearable :disabled=" false  ||ro.jingdianmingcheng"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="景点类型" prop="jingdianleixing">
            <el-select v-model="ruleForm.jingdianleixing" placeholder="请选择景点类型" :disabled=" false  ||ro.jingdianleixing" >
              <el-option
                  v-for="(item,index) in jingdianleixingOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点照片" v-if="type!='cross' || (type=='cross' && !ro.jingdianzhaopian)" prop="jingdianzhaopian">
            <file-upload
            tip="点击上传景点照片"
            action="file/upload"
            :limit="3"
            :multiple="true"
            :fileUrls="ruleForm.jingdianzhaopian?ruleForm.jingdianzhaopian:''"
            @change="jingdianzhaopianUploadChange"
            ></file-upload>
          </el-form-item>
            <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' class="upload" v-else label="景点照片" prop="jingdianzhaopian">
                <img v-if="ruleForm.jingdianzhaopian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.jingdianzhaopian.split(',')[0]" width="100" height="100">
                <img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.jingdianzhaopian.split(',')" :src="baseUrl+item" width="100" height="100">
            </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="景点等级" prop="jingdiandengji">
            <el-select v-model="ruleForm.jingdiandengji" placeholder="请选择景点等级" :disabled=" false  ||ro.jingdiandengji" >
              <el-option
                  v-for="(item,index) in jingdiandengjiOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="门票价格" prop="menpiaojiage">
			<el-input-number v-model="ruleForm.menpiaojiage" placeholder="门票价格" :disabled="ro.menpiaojiage"></el-input-number>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}'  label="景点省份" prop="shengfen">
            <el-select v-model="ruleForm.shengfen" placeholder="请选择景点省份" :disabled=" false  ||ro.shengfen" >
              <el-option
                  v-for="(item,index) in shengfenOptions"
                  :key="index"
                  :label="item"
                  :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点地址" prop="jingdiandizhi">
            <el-input v-model="ruleForm.jingdiandizhi" 
                placeholder="景点地址" clearable :disabled=" false  ||ro.jingdiandizhi"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="开放时间" prop="kaifangshijian">
            <el-input v-model="ruleForm.kaifangshijian" 
                placeholder="开放时间" clearable :disabled=" false  ||ro.kaifangshijian"></el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="购票须知" prop="goupiaoxuzhi">
            <el-input
              type="textarea"
              :rows="8"
              placeholder="购票须知"
              v-model="ruleForm.goupiaoxuzhi">
            </el-input>
          </el-form-item>
          <el-form-item :style='{"padding":"10px","margin":"0 0 10px","background":"none"}' label="景点详情" prop="jingdianxiangqing">
            <editor 
                :style='{"padding":"0","boxShadow":"none","margin":"0","borderColor":"#E2E3E5","backgroundColor":"none","borderRadius":"0","borderWidth":"1px","width":"100%","borderStyle":"solid","height":"auto"}'
                v-model="ruleForm.jingdianxiangqing" 
                class="editor" 
                action="file/upload">
            </editor>
          </el-form-item>

      <el-form-item :style='{"padding":"0","margin":"0"}'>
        <el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"rgba(255, 255, 255, 1)","borderRadius":"5px","background":"#A293B6","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}'  type="primary" @click="onSubmit">提交</el-button>
        <el-button :style='{"border":"none","cursor":"pointer","padding":"0","margin":"0","outline":"none","color":"#fff","borderRadius":"5px","background":"#9e9e9e","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' @click="back()">返回</el-button>
      </el-form-item>
    </el-form>
</div>
</template>

<script>
  export default {
    data() {
	  let self = this
      return {
        id: '',
        baseUrl: '',
        ro:{
				jingdianbianhao : false,
				jingdianmingcheng : false,
				jingdianleixing : false,
				jingdianzhaopian : false,
				jingdiandengji : false,
				menpiaojiage : false,
				shengfen : false,
				jingdiandizhi : false,
				kaifangshijian : false,
				goupiaoxuzhi : false,
				jingdianxiangqing : false,
				clicktime : false,
				clicknum : false,
				discussnum : false,
				storeupnum : false,
        },
        type: '',
        userTableName: localStorage.getItem('UserTableName'),
        ruleForm: {
          jingdianbianhao: this.getUUID(),
          jingdianmingcheng: '',
          jingdianleixing: '',
          jingdianzhaopian: '',
          jingdiandengji: '',
          menpiaojiage: '',
          shengfen: '',
          jingdiandizhi: '',
          kaifangshijian: '',
          goupiaoxuzhi: '',
          jingdianxiangqing: '',
          clicktime: '',
          clicknum: '',
          discussnum: '',
          storeupnum: '',
        },
        jingdianleixingOptions: [],
        jingdiandengjiOptions: [],
        shengfenOptions: [],


        rules: {
          jingdianbianhao: [
          ],
          jingdianmingcheng: [
          ],
          jingdianleixing: [
          ],
          jingdianzhaopian: [
          ],
          jingdiandengji: [
          ],
          menpiaojiage: [
            { validator: this.$validate.isNumber, trigger: 'blur' },
          ],
          shengfen: [
          ],
          jingdiandizhi: [
          ],
          kaifangshijian: [
          ],
          goupiaoxuzhi: [
          ],
          jingdianxiangqing: [
          ],
          clicktime: [
          ],
          clicknum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          discussnum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
          storeupnum: [
            { validator: this.$validate.isIntNumer, trigger: 'blur' },
          ],
        },
		centerType: false,
      };
    },
    computed: {



    },
    components: {
    },
    created() {
		if(this.$route.query.centerType){
			this.centerType = true
		}
	  //this.bg();
      let type = this.$route.query.type ? this.$route.query.type : '';
      this.init(type);
      this.baseUrl = this.$config.baseUrl;
    },
    methods: {
      getMakeZero(s) {
          return s < 10 ? '0' + s : s;
      },
      // 下载
      download(file){
        window.open(`${file}`)
      },
      // 初始化
      init(type) {
        this.type = type;
        if(type=='cross'){
          var obj = JSON.parse(localStorage.getItem('crossObj'));
          for (var o in obj){
            if(o=='jingdianbianhao'){
              this.ruleForm.jingdianbianhao = obj[o];
              this.ro.jingdianbianhao = true;
              continue;
            }
            if(o=='jingdianmingcheng'){
              this.ruleForm.jingdianmingcheng = obj[o];
              this.ro.jingdianmingcheng = true;
              continue;
            }
            if(o=='jingdianleixing'){
              this.ruleForm.jingdianleixing = obj[o];
              this.ro.jingdianleixing = true;
              continue;
            }
            if(o=='jingdianzhaopian'){
              this.ruleForm.jingdianzhaopian = obj[o].split(",")[0];
              this.ro.jingdianzhaopian = true;
              continue;
            }
            if(o=='jingdiandengji'){
              this.ruleForm.jingdiandengji = obj[o];
              this.ro.jingdiandengji = true;
              continue;
            }
            if(o=='menpiaojiage'){
              this.ruleForm.menpiaojiage = obj[o];
              this.ro.menpiaojiage = true;
              continue;
            }
            if(o=='shengfen'){
              this.ruleForm.shengfen = obj[o];
              this.ro.shengfen = true;
              continue;
            }
            if(o=='jingdiandizhi'){
              this.ruleForm.jingdiandizhi = obj[o];
              this.ro.jingdiandizhi = true;
              continue;
            }
            if(o=='kaifangshijian'){
              this.ruleForm.kaifangshijian = obj[o];
              this.ro.kaifangshijian = true;
              continue;
            }
            if(o=='goupiaoxuzhi'){
              this.ruleForm.goupiaoxuzhi = obj[o];
              this.ro.goupiaoxuzhi = true;
              continue;
            }
            if(o=='jingdianxiangqing'){
              this.ruleForm.jingdianxiangqing = obj[o];
              this.ro.jingdianxiangqing = true;
              continue;
            }
            if(o=='clicktime'){
              this.ruleForm.clicktime = obj[o];
              this.ro.clicktime = true;
              continue;
            }
            if(o=='clicknum'){
              this.ruleForm.clicknum = obj[o];
              this.ro.clicknum = true;
              continue;
            }
            if(o=='discussnum'){
              this.ruleForm.discussnum = obj[o];
              this.ro.discussnum = true;
              continue;
            }
            if(o=='storeupnum'){
              this.ruleForm.storeupnum = obj[o];
              this.ro.storeupnum = true;
              continue;
            }
          }
        }else if(type=='edit'){
			this.info()
		}
        // 获取用户信息
        this.$http.get(this.userTableName + '/session', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            var json = res.data.data;
          }
        });
        this.$http.get('option/jingdianleixing/jingdianleixing', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.jingdianleixingOptions = res.data.data;
          }
        });
        this.jingdiandengjiOptions = "★,★★,★★★,★★★★,★★★★★".split(',')
        this.$http.get('option/shengfen/shengfen', {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.shengfenOptions = res.data.data;
          }
        });

		if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
			localStorage.removeItem('raffleType')
			setTimeout(() => {
				this.onSubmit()
			}, 300)
		}
      },

    // 多级联动参数
      // 多级联动参数
      info() {
        this.$http.get(`jingdianxinxi/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
          if (res.data.code == 0) {
            this.ruleForm = res.data.data;
          }
        });
      },
      // 提交
      onSubmit() {
			if(this.ruleForm.jingdianbianhao){
				this.ruleForm.jingdianbianhao = String(this.ruleForm.jingdianbianhao)
			}
			//更新跨表属性
			var crossuserid;
			var crossrefid;
			var crossoptnum;
			this.$refs["ruleForm"].validate(valid => {
				if(valid) {
					if(this.type=='cross'){
						var statusColumnName = localStorage.getItem('statusColumnName');
						var statusColumnValue = localStorage.getItem('statusColumnValue');
						if(statusColumnName && statusColumnName!='') {
							var obj = JSON.parse(localStorage.getItem('crossObj'));
							if(!statusColumnName.startsWith("[")) {
								for (var o in obj){
									if(o==statusColumnName){
										obj[o] = statusColumnValue;
									}
								}
								var table = localStorage.getItem('crossTable');
								this.$http.post(table+'/update', obj).then(res => {});
							} else {
								crossuserid=Number(localStorage.getItem('frontUserid'));
								crossrefid=obj['id'];
								crossoptnum=localStorage.getItem('statusColumnName');
								crossoptnum=crossoptnum.replace(/\[/,"").replace(/\]/,"");
							}
						}
					}
					if(crossrefid && crossuserid) {
						this.ruleForm.crossuserid=crossuserid;
						this.ruleForm.crossrefid=crossrefid;
						var params = {
							page: 1,
							limit: 10,
							crossuserid:crossuserid,
							crossrefid:crossrefid,
						}
						this.$http.get('jingdianxinxi/list', {
							params: params
						}).then(res => {
							if(res.data.data.total>=crossoptnum) {
								this.$message({
									message: localStorage.getItem('tips'),
									type: 'error',
									duration: 1500,
								});
								return false;
							} else {
								// 跨表计算


								this.$http.post(`jingdianxinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
									if (res.data.code == 0) {
										this.$message({
											message: '操作成功',
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.$router.go(-1);
											}
										});
									} else {
										this.$message({
											message: res.data.msg,
											type: 'error',
											duration: 1500
										});
									}
								});
							}
						});
					} else {


						this.$http.post(`jingdianxinxi/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(res => {
							if (res.data.code == 0) {
								this.$message({
									message: '操作成功',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				}
			});
		},
		// 获取uuid
		getUUID () {
			return new Date().getTime();
		},
		// 返回
		back() {
			this.$router.go(-1);
		},
      jingdianzhaopianUploadChange(fileUrls) {
          this.ruleForm.jingdianzhaopian = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
      },
    }
  };
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.el-date-editor.el-input {
		width: auto;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
	  padding: 0 10px 0 0;
	  color: #000;
	  font-weight: 500;
	  width: 100px;
	  font-size: 14px;
	  line-height: 40px;
	  text-align: center;
	}
	
	.add-update-preview .el-form-item /deep/ .el-form-item__content {
	  margin-left: 100px;
	}
	
	.add-update-preview .el-input /deep/ .el-input__inner {
	  border: 1px solid #E2E3E5;
	  border-radius: 30px;
	  padding: 0 12px;
	  box-shadow: none;
	  outline: none;
	  color: #000;
	  width: 500px;
